@import url(https://fonts.googleapis.com/css?family=Montserrat|Nunito);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

document {
    background-color: #FAFAFA
}

.App {
    display: flex;
    justify-content: center;
    font-family: Montserrat;
}

h1 {
    font-family: Nunito;
    font-style: normal;
    font-weight: normal;
    line-height: normal;
    font-size: 70px;
    color: #456B8A;
    margin-top: 0;
    margin-bottom: 0;
}

h3 {
    font-family: Montserrat;
    font-size: 20px;
    color: #456B8A;
}

.header-info {
    color: #575757;
}

.profile-pic {
    height: 12em;
    /* margin: auto; */
    display: block;
}

.header {
    margin-top: 2rem;
    margin-bottom: 6em;
    display: flex;
    justify-content: space-evenly;
    align-content: center;
}

.header-line {
  height: 5em;
  background-color: #456B8A;
  width: 0.4em;
  float: right
}

.text-holders {
    display: flex;
    margin-bottom: 6em;
    justify-content: space-between;
}

ul {
    list-style-type: none;
    padding: 0
}

.text-column {
    width: 20%;
    flex-direction: column;
}

.text-element-bottom {
    align-self: flex-end;
}

.text-element-top {
    align-self: flex-start
}

.project img {
    width: 100%;
    border: none;
    display: block;
}

.project {
    color: white;
    background-repeat: no-repeat;
    margin-bottom: 4em;
    position: relative;
}

.project h2{
  margin-bottom: 0;
}

/* .project p {
  margin-top: 0.1em;
} */

.ara {
    background-image: url("/static/media/Group 2.59229f42.png");
    border-right: 6px solid #03AB00;
}

.ara h2 {
    color: #03AB00
}

.evolution {
    background-image: url("/static/media/Group 3.3a5e972c.png");
    border-left: 6px solid #FF7557;
}

.evolution h2 {
    color: #FF7557
}

.univerhouse {
    background-image: url("/static/media/Group 4.4e2ba1f2.png");
    border-right: 6px solid #62EFFF;
}

.univerhouse h2 {
    color: #62EFFF;
}

.align-right {
    text-align: end;
    right: 1em;
}

.align-left { 
  left: 1em;
}

.sound {
    border-left: 6px solid #da0027;
}

.sound h2 {
    color: #da0027;
}

.project-content {
    width: 50%;
    position: absolute;
    top: 0;
}
